#contact-me {
    /* background-color: #060916; */
    position: relative;
    overflow: hidden;
}

.contact-title {
    font-weight: bold;
    font-size: 34px;
    font-family: 'Montserrat', sans-serif;
    color: #fff;
    letter-spacing: 0.2em;
}

.contact-title span {
    margin-left: 13px;
    position: relative;
    z-index: 99;
}

.white_line {
    width: 344px;
    height: 6px;
    background-color: #fff;
}

.contact-side {
    width: 50%;
    position: relative;
    padding: 110px 70px;
    overflow: hidden;
}

.contact-side[side="right"] {
    background-color: rgba(7, 10, 15, 0.9);
    z-index: 99;
}

.contact_bg_img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 110%;
    z-index: -2;
}

.contact-form {
    margin-top: 25px;
}

.contact_input {
    width: 100% !important;
    border-color: #fff !important;
}

.contact_input label {
    color: #fff !important;
}

.contact_input .MuiInputBase-input {
    color: #fff !important;
    font-family: 'Montserrat', sans-serif !important;
    font-size: 16px !important;
    border: 2px solid #fff !important;
    border-radius: 5px !important;
}

.contact_input .MuiInputLabel-outlined.MuiInputLabel-shrink {
    background-color: rgba(7, 10, 15, 0.308) !important;
    padding: 0 8px !important;
}

.contact_input .Mui-focused {
    outline: none !important;
    border: none !important;
}

.contact-message {
    width: 100%;
    border-radius: 5px;
    border: 2px solid #fff;
    height: 200px;
    background-color: transparent;
    color: rgba(255, 255, 255, 0.781);
    padding: 20px 10px;
    margin-top: 15px;
    font-size: 16px;
}

.contact-message::placeholder {
    font-family: 'Montserrat', sans-serif !important;
    color: #fff !important;
    font-size: 16px;
}

.my_contact-details {
    display: flex;
    margin-bottom: 15px;
    color: #fff;
    font-family: 'Montserrat', sans-serif !important;
    font-size: 22px;
    align-items: center;
}

.my_contact-details span:first-child {
    font-weight: bold;
    color: #F6AE2D;
    margin-right: 5px;
    min-width: 100px;
    display: block;
}

.empty__line{
    display: flex;
    margin-bottom: 30px;
}

.empty__line span{
    display: block;
    height: 10px;
}

.empty__line span:first-child{
    width: 35px;
    background-color: #fff;
}

.empty__line span:last-child{
    width: 80px;
    background-color: #F6AE2D;
    margin: 0 10px;
}

